/*
 * Layout-related sections
 * ----------------------- */
body.bg-gradient-blue {
  background-attachment: fixed;
}

.bg-gradient-blue {
  background: linear-gradient(to right, #0c2646 0%, #204065 60%, #2a5788 100%);
  background: -moz-linear-gradient(to right, #0c2646 0%, #204065 60%, #2a5788 100%);
  background: -webkit-linear-gradient(to right, #0c2646 0%, #204065 60%, #2a5788 100%);
  background: -o-linear-gradient(to right, #0c2646 0%, #204065 60%, #2a5788 100%);
  background: -ms-linear-gradient(to right, #0c2646 0%, #204065 60%, #2a5788 100%);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.bg-unsplash {
  background-image: url("https://source.unsplash.com/1280x720/daily?programming");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

